  
#main {
  width: 960px;
  height: 400px;
  background-image: url("./images/advertise-advertisement-advertising-announcement-article-articles-1437949-pxhere.com.jpg");
  background-size: 100% auto;
  background-repeat: no-repeat;
  margin-left: auto;
  margin-right: auto;
  padding-left:20px;
  padding-top: 5%;
  align-items: center;
  justify-content: center;
}

#root {
  width: 960px;
  margin: 20px auto;
}


#main_title,
#main_subtitle,
#main_description {
  color: white;
  text-align: center;      
}

#main_title {
  font-size: 60px;
  margin: 130px auto 20px auto;
}

#main_subtitle {
  margin-top: 20px;
  font-size: 35px;
}

#main_description {
  font-size: 25px;
  margin-top: 20px;
}


#articles {
  display: inline-block;
  overflow: auto;
  width: 60%;
  height: 700px;
}


#search,
#results,
#saved {
  width:960px;
  height: 200px;
  background-color: rgb(206, 230, 245);
  margin: 20px auto;
}

#btnSaved {
  float: right;
}

.navigation {
  width: 700px;
  height: 60px;
  padding: 20px 0 20px 0;
  font-size: 16px;
  text-align: center;
  float: right;
}


body { background-color: rgb(45, 100, 202) }

button {
float: right;
background-color: rgb(5, 60, 148);
color: white;
border: 0.5px;
border-radius: 3px;

}

a {
color: rgb(5, 60, 148);
padding-top: 0px;
padding: 0 20px 0 20px;
float: right;
text-decoration: none;
}

p {
margin: 5px;
}

p, h4 {
  background-color: rgb(206, 230, 245);
  padding: 5px;
}

